// export default useObsolescenceDropableArea;

import { useCallback } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  setDroppedObjectData,
  setLoading,
  setIsDropped as setIsDroppedAction,
} from "../store/droppedObjectSlice";
import useToast from "../hooks/useToast";
import useObsolescenceWidget from "./useObsolescenceWidget";
import {
  initializeDroppableArea as initDroppable,
  getDroppedObjectDetails,
} from "../services/api/droppableService";
import {
  MSG_FETCH_OBJECT_DETAILS_FAILED,
  MSG_UNEXPECTED_ERROR,
} from "../utils/toastMessages";

const useObsolescenceDropableArea = () => {
  const { showErrorToast } = useToast();
  const { handleObsolescenceWidget } = useObsolescenceWidget();
  const isDropped = useSelector((state) => state.droppedObject.isDropped);
  const loading = useSelector((state) => state.droppedObject.loading);
  const dispatch = useDispatch();

  // Define valid types for CA or Engg Item
  const validTypes = [
    "VPMReference",      // Valid Type 1
    "Engg Item",         // Valid Type 2
    "Physical Product",  // Valid Type 3
    "Change Action",     // Valid Type 4 (CA)
  ];

  const fetchObjectDetails = useCallback(
    async (dataItems) => {
      try {
        const objectDetailsResult = await getDroppedObjectDetails({ dataItems });

        if (objectDetailsResult.success) {
          const draggedObjectData = objectDetailsResult.data.cardData;
          console.log("[Dragged Items are]", draggedObjectData);

          // ✅ Validate object type before proceeding
          const objectType = draggedObjectData?.Type;

          // Check if the object type is valid (Engg Item, Physical Product, VPMReference, or Change Action)
          if (!validTypes.includes(objectType)) {
            showErrorToast("Please select a CA or Engg Item.");
            dispatch(setLoading(false));
            return;
          }

          // ✅ Check if the Maturity State is not 'Released'
          const maturityState = draggedObjectData?.["Maturity State"];
          if (maturityState !== "Released") {
            showErrorToast("Product is not released.");
            dispatch(setLoading(false));
            return;
          }

          // Proceed with setting the dropped object data
          dispatch(setDroppedObjectData({ cardData: draggedObjectData }));
          dispatch(setIsDroppedAction(true));

          // Call handleObsolescenceWidget after successful validation
          await handleObsolescenceWidget(
           // draggedObjectData["Collaborative Space"],
           // draggedObjectData["Maturity State"],
            dataItems[0]?.objectId,
            dataItems[0]?.objectType,
            draggedObjectData["relativePath"],
            // draggedObjectData.Name,
          //  draggedObjectData["Dropped Revision"]
          );
        } else {
          showErrorToast(MSG_FETCH_OBJECT_DETAILS_FAILED);
        }
      } catch (error) {
        console.error("[FetchObjectDetails] Error fetching details:", error);
        showErrorToast(MSG_FETCH_OBJECT_DETAILS_FAILED);
      } finally {
        dispatch(setLoading(false));
      }
    },
    [dispatch, handleObsolescenceWidget, showErrorToast]
  );

  const handleDrop = useCallback(
    async (dataItems) => {
      dispatch(setLoading(true)); // Start loading state
      console.log("[handleDrop] handleDrop called with dataItems:", dataItems);
      try {
        if (dataItems && dataItems.length > 0) {
          await fetchObjectDetails(dataItems);
        } else {
          console.warn("[handleDrop] No data items to process.");
        }
      } catch (error) {
        dispatch(setLoading(false));
        console.error("[Drop] Error in handleDrop:", error);
        showErrorToast(MSG_UNEXPECTED_ERROR);
      }
    },
    [fetchObjectDetails, showErrorToast, dispatch]
  );

  const initializeDroppableArea = useCallback(() => {
    if (!isDropped) {
      console.log("[initializeDroppableArea] 🚀 Resetting isDropped to false...");
      dispatch(setIsDroppedAction(false));
    } else {
      console.log("[initializeDroppableArea] ⏳ isDropped is already true. Skipping reset.");
    }

    const interval = setInterval(() => {
      const droppableContainer = document.querySelector(".droppable-container");
      if (droppableContainer) {
        clearInterval(interval);
        initDroppable(droppableContainer, handleDrop, dispatch, showErrorToast);
      }
    }, 100);

    return () => clearInterval(interval); // Cleanup
  }, [handleDrop, dispatch, isDropped, showErrorToast]);

  return {
    initializeDroppableArea,
    loading,
    handleDrop,
  };
};

export default useObsolescenceDropableArea;
